<template>
  <div class="ocpspc-transferCard-box">
    <div class="ocps-transferCard" >
      <div class="ocps-transferCard-bg ocps-blue-border bg-transferCard">
        <div class="ocps-transferCard-lf">
          <!--<p class="ocps-transferCard-name" v-if="this.$route.query.type == 1"> </p>-->
          <!--<p class="ocps-transferCard-name" v-if="this.$route.query.type == 0"> </p>-->
          <p class="ocps-transferCard-name"> </p>

        </div>
        <div class="ocps-transferCard-rt">
          <p class="ocps-transferCard-number">1</p>
          <div class="ocps-transferCard-operation">
            <p class="cops-bankNumber">2</p>
            <p class="cops-bank-balance">
              
            </p>
          </div>
        </div>
      </div>
     </div>
    <form @submit.prevent="submitFn">
      <ul class="ocps-inputbox">
      <li>
        <div class="ocps-bind_title"><span class="red">*</span></div>
        <div class="ocps-bindbox">
          <input type="text" readonly v-model="this.$route.query.FcardNo" @keyup="filterNumber" @afterpaste="filterNumber" maxlength="16" class="ocps-none-input"  placeholder=""/>
        </div>
      </li>
      <li>
        <div class="ocps-bind_title"><span class="red">*</span></div>
        <div class="ocps-bindbox">
          <input type="text" v-model="TransferInfo.money" maxlength="8" @blur="reg_money" class="ocps-none-input" placeholder=""/>
        </div>
      </li>
      <li>
        <div class="ocps-bind_title"><span class="red">*</span></div>
        <div class="ocps-bindbox">
          <input type="password" v-model="TransferInfo.password" maxlength="6"   @keyup="filterNumber" @afterpaste="filterNumber"  class="ocps-none-input" placeholder=""/>
        </div>
      </li>
    </ul>
      <div class="button_box">
        <input type="submit" value="" class=" ocps-blue-button">
      </div>
    </form>
  </div>
</template>
<script>
  import qs from 'qs'
  export default{
    name: 'EntityCard',
    data(){
    return {
      TransferInfo:{
        money:'',
        password:'',
        cardNo:'',
        fCardNo:''
      }
    }
  },
  methods: {
    filterNumber () {
      this.TransferInfo.cardNo=this.TransferInfo.cardNo.replace(/\D/g,'')
      this.TransferInfo.password=this.TransferInfo.password.replace(/\D/g,'')
    },
     reg_money(){
       var reg =/(^((0|[1-9][0-9]{0,7})([.]\d{1,2})?)$)/;
       if(!reg.test(this.TransferInfo.money)){
          this.TransferInfo.money=""
          return false
        }
     },
    submitFn :function(){
      this.TransferInfo.cardNo=this.$route.query.mainCardNo
      this.TransferInfo.fCardNo=this.$route.query.FcardNo
      var url=this.$store.state.localHostUrl + '/accountTrasfer.json';
      console.log(this.TransferInfo)
      var reg =/(^((0|[1-9][0-9]{0,7})([.]\d{1,2})?)$)/;
      if(this.TransferInfo.money === ""){
       this.$confirm('!', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info',
            showCancelButton: false
         })
            return false
      }
      if(!reg.test( this.TransferInfo.money)){
          this.$confirm('!', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info',
            showCancelButton: false
         })
            return false
      }
      if(this.TransferInfo.password === ""){
           this.$confirm('!', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info',
            showCancelButton: false
         })
            return false
      }
       if(this.TransferInfo.password.length !== 6){
             this.$confirm('6!', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info',
            showCancelButton: false
         })
              return false
      }
      else{
        var formDate=qs.stringify(this.TransferInfo);
        var that=this
        this.$http.post(url,formDate, {emulateJSON: true}).then(
          function (res){
            if(res.data.ret){
              alert(res.data.tip)
              that.$store.dispatch('getcardBalance')
            }else{
              alert(res.data.tip)
            }
          }
        )
      }
    }
  }
  }

</script>



